$primaryColor: #4527a4;

// Dashicon
.dashicons {
	font-family: dashicons !important;
}

// Lottie Player
.wp-block-lpb-lottie-player {
	* {
		box-sizing: border-box;
	}

	.lpbLottiePlayer {
		dotlottie-player {
			display: inline-flex;
			max-width: 100%;
		}

		.caption {
			max-width: 100%;
			display: inline-block;
		}
	}
}